.app-footer {
  padding-top: 8.5416666667vw;
}

.app-footer-container {
  background-color: #2d331b;
  padding: 3.59375vw 0;
}

.app-footer-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.9895833333vw;
}

.app-footer-row .phone {
  font-size: 1.4583333333vw;
}

.app-footer-row .logo {
  width: 6.1458333333vw;
  aspect-ratio: 118/30;
}

.app-footer-row:first-child {
  position: relative;
  margin-bottom: 3.28125vw;
}

.app-footer-row:first-child::after {
  content: "";
  display: inline-block;
  width: 94.5833333333vw;
  height: 1px;
  background-color: var(--primary-1);
  position: absolute;
  bottom: -1.6666666667vw;
  left: 50%;
  transform: translateX(-50%);
}